<template>
  <div class="main_navs">
    <Header></Header>

    <div class="container">
      <div class="new_reg">

        <div class="log_form dark_main_reg">
          <div>
            <div class="reg_people_title mt-4 text-center text-white">
              Oddiy foydalanuvchi
            </div>
            <form @submit.prevent="regIstration()">
              <label class="text-white">{{$t('email')}} *</label>
              <div class="input_text ">
                <input type="email" v-model="email" placeholder="Email">
              </div>
              <label class="text-white">{{$t('password')}}*</label>
              <div class="input_text">
                <input type="password" v-model="password" :placeholder="$t('password')">
              </div>
              <div class="forgot_password">
                <router-link to="/" class="decoration text-white">
                  {{$t('forget_password')}}
                </router-link>
              </div>
              <div class="log_btn">
                <button> {{$t('reg')}}</button>
              </div>
            </form>
            <div class="reg_links">
              <router-link class="text-decoration-none" to="/email-login">
                <div class="text-white">{{$t('reg_quiz')}}? <span class="text-white">{{$t('login')}}</span></div>
              </router-link>
            </div>
          </div>
        </div>
      </div>
    </div>
    <Footer></Footer>
  </div>
</template>
<script>
import Header from "../../components/Header";
import Footer from "../../components/Footer";

export default {
  components: {Footer, Header},
  data() {
    return {
      email: '',
      password: ''
    }
  },
  methods: {
    regIstration() {

      this.$axiosDefault.post('/students/register/', {
        "email": this.email,
        "password": this.password
      })
          .then(() => {
            this.$toast("Emailga xabar jo'natildi iltimos tekshirib ko'ring", {
              timeout: 10000,
              type: "success"
            });
          })
          .catch(error => {
            console.log(error)
            this.$toast(`${error.response.data.email},${error.response.data.password}`, {
              timeout: 10000,
              type: "error"
            });
          })

    }
  }

}
</script>
